@keyframes Bump {
  0% {
    transform: scale(1.015); }
  50% {
    transform: scale(0.8); }
  100% {
    transform: scale(1); } }

@keyframes wave {
  0% {
    transform: translateX(1px); }
  10% {
    transform: translateX(4px); }
  25% {
    transform: translateX(7px); }
  55% {
    transform: translateX(4px); }
  75% {
    transform: translateX(1px); }
  85% {
    transform: translateX(-2px); }
  100% {
    transform: translateX(1px); } }

html {
  box-sizing: border-box;
  background-color: #3767ad; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  background-color: #3767ad;
  font-family: "Roboto", sans-serif;
  min-height: 100vh; }

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh; }

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 0; }
  .container__header {
    color: wheat; }

.container2 {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transform: scale(0.175);
  max-height: 98px; }
  .container2__header {
    transform: scale(2);
    margin-bottom: 56px; }

.button {
  display: flex;
  flex-direction: column;
  margin-top: 48px;
  transition: background 2s ease-in-out; }
  .button__add {
    font-weight: bold;
    font-size: 14px;
    width: 216px;
    height: 47px;
    border-radius: 4px;
    background-color: fffff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border: 1px solid #ffffff; }
    .button__add:hover {
      background: #eafffd;
      transform: scale(1.01); }
    .button__add:active {
      animation: Bump 2s ease-in-out; }
  .button__remove {
    margin-top: 37px;
    background: none;
    border: none;
    color: #f5e5e7;
    font-weight: lighter;
    font-size: 14px; }
    .button__remove:hover {
      color: #ff6f59; }

.glass__count, .glass__count-2 {
  display: flex;
  font-family: "Odibee Sans";
  justify-content: center;
  margin-top: -140px;
  background: none;
  border: none;
  font-family: Roboto;
  font-weight: 500;
  font-size: 112px;
  line-height: 131px; }

.glass__water {
  animation: none; }

.glass__water--active {
  animation: wave 1s ease-in-out; }

.table {
  display: grid;
  margin-top: 48px;
  grid-template-areas: "caption caption" "header header" "content content";
  grid-template-columns: 200px 1 fr; }
  .table__a {
    grid-area: content; }
  .table__b {
    grid-area: content; }
  .table__caption {
    grid-area: caption; }
  .tablelist {
    background-color: whitesmoke; }
  .table__yesterday {
    grid-area: header;
    background-color: yellow; }
  .table__today {
    grid-area: header;
    background-color: yellowgreen; }
